@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.app {
  text-align: center;
}

.app h1 {
  display: block;
  min-height: 600px;
}
@media screen and (min-width: 857px) {
    a.header-menu-button {
        display: none;
    }

    div.dropdown-menu {
        display: none;
    }
}

@media screen and (max-width: 640px) {
    div.header-menu-bar{
       width: 600px;
       margin: 10px;
    }

    div.header-menu {
      display: none;
    }

    div.dropdown-menu {
        display: none;
    }

    a.header-menu-button {
        display: block;
        margin: 0 auto;
        padding: 20px 5px 0px 5px;
    }

    div.dropdown-menu.mobile {
        display: block;
    }

    div.dropdown-menu.mobile a {
        display: block;
        padding: 0px;
    }

}

@media screen and (max-width: 856px) {
    div.header-menu-bar{
       width: 800px;
       margin: 10px;
    }

    div.header-menu {
      display: none;
    }

    div.dropdown-menu {
        display: none;
    }

    a.header-menu-button {
        display: block;
        margin: 0 auto;
        padding: 20px 5px 0px 5px;
    }
}

.header-back {
    width: 100%;
    background: linear-gradient(135deg, #32cbfa 0%,#2669f0 100%);
    min-height: 131px;
    margin: 0;
    padding-top: 65px;
    overflow: visible;
}

.container {
    width: 70%;
    margin: 0 auto;
    min-width: 300px;
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: white;
    height: 64px;
    border-radius: 36px;
    position: relative;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

nav a {
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 600;
    font-size: 20px;
    height: 44px;
    cursor: pointer;
    text-decoration: none;
    transition: 0.5s;
}

nav a:hover {
    color: #2669f0; 
}
  
.home-button{
    float: left;
    min-width: 32px;
    height: 32px;
    background: #2669f0;
    border-radius: 16px;
    margin: 16px;
    transition: 0.5s ease;
    cursor: pointer;
}

.home-button .material-icons {
    color: white;
    display: inline-block;
    font-size: 24px;
    margin: 4px;
}

.authorization {
    float: right;
    min-width: 32px;
    height: 32px;
    background: #2669f0;
    border-radius: 16px;
    margin: 16px;
    transition: 0.5s ease;
    cursor: pointer;
}

.authorization .material-icons {
    color: white;
    display: inline-block;
    font-size: 28px;
    margin: 2px;
}

.home-button:hover {
    background: #32cbfa;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.authorization:hover {
    background: #32cbfa;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.header-menu {
    height: 64px;
    min-width: 472px;
    width: 640px;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-menu a {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 600;
    font-size: 20px;
    color: black;
    display: block;
    height: 44px;
    cursor: pointer;
    text-decoration: none;
    padding: 20px 5px 0px 5px;
    transition: 0.5s;
}

.header-menu a:hover {
    color: #2669f0; 
}

.dropdown-menu.mobile {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    background: white;
    width: 100%;
    border-radius: 32px;
    margin: 72px auto;
    position: absolute;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    -webkit-animation: fade-bottom ease 0.3s;
            animation: fade-bottom ease 0.3s;
}

.dropdown-menu.mobile a {
    text-align: center;
    padding: 0 16px;
    margin-top: 20px;
}

@-webkit-keyframes fade-bottom {
    from {
      opacity: 0;
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

@keyframes fade-bottom {
    from {
      opacity: 0;
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
@-webkit-keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(60deg);
        transform: rotate(60deg);
    }
} 

@keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(60deg);
        transform: rotate(60deg);
    }
}

h2.min-width-message {
    color: #2669f0;
    display: block;
    padding: 5px;
    text-align: center;
}

.rotate-image i.material-icons {
    display: block;
    color: #2669f0;
    font-size: 64px;
    margin: 0 auto;
}

.rotate-image {
    height: 64px;
    width: 64px;
    margin: 0 auto;
    -webkit-animation: rotating 1s infinite ease-in-out;
            animation: rotating 1s infinite ease-in-out;
}


